import { useEffect, useRef, useState } from 'react';
import Layout from '../common/Layout';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';

function Signup() {
	const history = useHistory();
	const initVal = useRef({
		userid: '',
		pwd1: '',
		pwd2: '',
		email: '',
		gender: '',
		hobbys: [],
		edu: '',
		comments: '',
	});
	const [val, setValue] = useState(initVal.current);
	const [Err, setErr] = useState({});
	const [Submit, setSubmit] = useState(false);

	// input ,select
	const onChangeValue = (e) => {
		const { name, value } = e.target;
		console.log(name, value);
		setValue({ ...val, [name]: value });
	};

	const onChangeCheck = (e) => {
		const { name } = e.target;

		const checks = [...e.target.parentElement.querySelectorAll('input')]
			.filter((el) => el.checked)
			.map((el) => {
				return el.value;
			});

		setValue({ ...val, [name]: checks });
		console.log(val);
	};

	const onChangeRadio = (e) => {
		const { name, checked } = e.target;
		setValue({ ...val, [name]: checked });
	};
	const validate = (value) => {
		const errs = {};
		const eng = /[a-zA-Z]/;
		const num = /[0-9]/;
		const spc = /[~!@#$%^&*()_+]/;
		const regex = /(\W|^)[\w.\-]{0,25}@(naver|daum|gmail)\.com(\W|$)/;

		if (value.userid.length < 5) {
			errs.userid = ' 5  .';
		}
		if (
			value.pwd1.length < 5 ||
			!eng.test(value.pwd1) ||
			!num.test(value.pwd1) ||
			!spc.test(value.pwd1)
		) {
			errs.pwd1 = ' 5 , , ,   .';
		}
		if (value.pwd1 !== value.pwd2 || !value.pwd2) {
			errs.pwd2 = '   .';
		}
		if (!regex.test(value.email)) {
			errs.email = '   .';
		}
		if (value.gender === '') {
			errs.gender = ' .';
		}
		if (value.hobbys.length === 0) {
			console.log(value.hobbys);
			errs.hobbys = ' .';
		}

		if (value.edu === '') {
			errs.edu = ' .';
		}
		if (value.comments.length < 10) {
			errs.comments = '   10  .';
		}
		return errs;
	};
	const formSubmit = (e) => {
		e.preventDefault();
		setErr(validate(val));
		setSubmit(true);
	};

	// validation   
	useEffect(() => {
		const len = Object.keys(Err).length;
		if (len === 0 && Submit) {
			alert('  .');
			setValue(initVal);
			history.push('/');
		}
	}, [history, Submit, Err]);

	return (
		<Layout name={'Signup'}>
			<form onSubmit={formSubmit}>
				<fieldset>
					<table>
						<tbody>
							<tr>
								<th scope='row'>
									<label htmlFor='userId'></label>
								</th>
								<td>
									<input
										type='text'
										name='userid'
										id='userid'
										placeholder=' .'
										onChange={onChangeValue}
										value={val.userid || ''}
									/>
									<br />
									{Err.userid && <p>{Err.userid}</p>}
								</td>
							</tr>

							<tr>
								<th scope='row'>
									<label htmlFor='pwd1'></label>
								</th>
								<td>
									<input
										type='password'
										name='pwd1'
										id='pwd1'
										placeholder=' .'
										onChange={onChangeValue}
										value={val.pwd1 || ''}
									/>
									<br />
									{Err.pwd1 && <p>{Err.pwd1}</p>}
								</td>
							</tr>
							<tr>
								<th scope='row'>
									<label htmlFor='pwd2'> </label>
								</th>
								<td>
									<input
										type='password'
										name='pwd2'
										id='pwd2'
										placeholder='  .'
										onChange={onChangeValue}
										value={val.pwd2 || ''}
									/>
									<br />
									{Err.pwd2 && <p>{Err.pwd2}</p>}
								</td>
							</tr>

							<tr>
								<th>
									<label htmlFor='email'></label>
								</th>
								<td>
									<input
										type='email'
										name='email'
										id='email'
										placeholder='  .'
										onChange={onChangeValue}
										value={val.email || ''}
									/>
									<br />
									{Err.email && <p>{Err.email}</p>}
								</td>
							</tr>

							<tr>
								<th scrope='row'>
									<label htmlFor='edu'></label>
								</th>
								<td>
									<select name='edu' id='edu' onChange={onChangeValue}>
										<option value=''> </option>
										<option value='elementary school'> </option>
										<option value='middle school'> </option>
										<option value='high school'> </option>
										<option value='college'> </option>
									</select>
									<br />
									<p>{Err.edu}</p>
								</td>
							</tr>

							<tr>
								<th scope='row'></th>
								<td>
									<label htmlFor='male'>Male</label>
									<input
										type='radio'
										name='gender'
										id='male'
										value='male'
										onChange={onChangeRadio}
									/>

									<label htmlFor='female'>Female</label>
									<input
										type='radio'
										name='gender'
										id='female'
										value='female'
										onChange={onChangeRadio}
									/>
									<br />
									{Err.gender && <p>{Err.gender}</p>}
								</td>
							</tr>

							<tr>
								<th scope='row'></th>
								<td>
									<label htmlFor='sports'>Sports</label>
									<input
										type='checkbox'
										name='hobbys'
										id='sports'
										value='sports'
										onChange={onChangeCheck}
									/>

									<label htmlFor='music'>Music</label>
									<input
										type='checkbox'
										name='hobbys'
										id='music'
										value='music'
										onChange={onChangeCheck}
									/>

									<label htmlFor='game'>Game</label>
									<input
										type='checkbox'
										name='hobbys'
										id='game'
										value='game'
										onChange={onChangeCheck}
									/>

									<label htmlFor='reading'>Reading</label>
									<input
										type='checkbox'
										name='hobbys'
										id='reading'
										value='reading'
										onChange={onChangeCheck}
									/>
									<br />
									{Err.hobbys && <p>{Err.hobbys}</p>}
								</td>
							</tr>

							<tr>
								<th>
									<label htmlFor='comments'></label>
								</th>
								<td>
									<textarea
										name='comments'
										id='comments'
										cols='30'
										rows='4'
										placeholder='  .'
										maxLength='200'
										onChange={onChangeValue}
									></textarea>
									<br />
									<p>{Err.comments}</p>
								</td>
							</tr>

							<tr>
								<th colSpan='2'>
									<input
										type='reset'
										value=''
										onClick={() => setValue(initVal)}
									/>
									<input type='submit' value='' />
								</th>
							</tr>
						</tbody>
					</table>
				</fieldset>
			</form>
		</Layout>
	);
}

export default Signup;
